import React from 'react'

import { TextField, Button } from '../Components/Inputs'
import MdArrowBack from 'react-icons/lib/md/arrow-back'

const shared = { maxWidth: '60%', minWidth: '50%', padding: '5px', height: '50px', margin: '10px', border: '1px solid white', borderRadius: '3px' }
const sentStyle = { backgroundColor: '#DDD', float: 'right', ...shared }
const recieveStyle = { backgroundColor: 'green', color: 'white', float: 'left', ...shared }
const ChatMessage = ({ sent, body }) => (<div style={{ height: '50px', display: 'block', width: '100%'}}><span style={sent ? sentStyle : recieveStyle}>{body}</span></div>)

const Chat = ({ name, messages, clearChat }) => (<div style={{ overflow: 'scroll', backgroundColor: '#EEE', border: '5px solid #FFF', minWidth: '300px', maxWidth: '425px', margin: 'auto', minHeight: '90vh' }}>
  <div style={{
    fontWeight: 'bold', fontSize: '24px', backgroundColor: '#001933', color: 'white'
  }}><span style={{ float: 'left', }} onClick={clearChat}><MdArrowBack /></span>{name}</div>
  <div style={{ position: 'absolute', bottom: '30px', width: '100%', maxWidth: '425px', margin: 'auto' }}>{messages.map((chatMessage, index) => (
    <ChatMessage {...chatMessage} key={index} />  
  ))}</div>
  <div style={{ position: 'absolute', bottom: '6px', margin: 'auto', width: '100%', maxWidth: '425px', backgroundColor: 'white' }}>
    <TextField style={{ }} inline /><Button style={{  }} inline label='Send' />
  </div>
</div>)

const Message = ({ name, last, picture, messages, setCurrent }) => (
  <div onClick={() => setCurrent({name, messages})} style={{ borderTop: '1px solid #DDD', borderBottom: '1px solid #DDD', height: '50px' }}>
    <img src={picture} style={{ float: 'left',display: 'inline-block', height: '40px', width: '40px', lineHeight: '50px', verticalAlign: 'center', borderRadius: '100px', marginTop: '5px', marginLeft: '10px' }} />
    <div style={{ display: 'inline-block', fontWeight: 'bold', float: 'left', textAlign: 'left', marginLeft: '10px', verticalAlign: 'top', lineHeight: '50px' }}>{name}</div>
  </div>
)

class Messages extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      currentChat: {}
    }

    this.setCurrentChat = this.setCurrentChat.bind(this)
  }

  setCurrentChat(message) {
    this.setState({
      currentChat: message
    })
  }

  render() {
    const { messages } = this.props
    const { currentChat } = this.state
    return (
      <div>
        {currentChat.messages ? 
        <Chat clearChat={() => this.setState({ currentChat: {} })} {...currentChat} /> : 
        <div style={{ minWidth: '300px', maxWidth: '425px', minHeight: '100vh', backgroundColor: 'white', margin: 'auto' }}>
          <div>Messages</div>
          {messages.map((message, index) => (
            <Message setCurrent={this.setCurrentChat} {...message} />
          ))}
        </div>}
      </div>
    )
  }
}

export default Messages



