import React from 'react'

const Button = ({ handleClick, label, color, labelColor, width, style, inline, link }) => (
  <div href={link} style={{
    display: inline ? 'inline-block' : '',
    margin: inline || '5px auto',
    color: labelColor || '#FFF',
    backgroundColor: color || '#099',
    width: width || '100px',
    boxShadow: '1px 1px #AAA, -1px 1px 3px #AAA',
    ...style,
  }} onClick={handleClick}>{label}</div>)

const TextField = ({handleChange, password, style, hintText, value}) => (
  <input 
    type={password && "password"}
    placeholder={hintText} 
    style={{ margin: '5px', ...style}} 
    onChange={handleChange} 
    value={value} />)

const Form = ({ children, style }) => (
  <div style={{ padding: 'auto 10px', ...style }}>
    {children}
  </div>)

export { Button, TextField, Form }



