import React from 'react'

const Header = ({ currPage, toggleDrawer }) => (
  <div style={{ 
    paddingLeft: '15px',
    textAlign: 'left',
    height: '50px',
    fontSize: '45px',
    backgroundColor: '#001933', color: '#FFF',
    lineHeight: '50px'
  }}>
    Collr
    <span onClick={toggleDrawer} style={{
      height: '28px',
      width: '32px' ,
      backgroundColor: '#D5E8D4',
      border: '1px solid #D5E8D4',
      float: 'right', 
      margin: '10px 15px',
      borderRadius: '3px'
    }}></span>
  </div>)

export default Header


