import React from 'react'
import { Paper } from '../Components/Formatting'
import { Form, Button, TextField } from '../Components/Inputs'

const SignIn = ({ 
  handleChangePassword, 
  handleChangeUsername, 
  handleSubmitSignIn, 
  username, 
  password, 
  failedLogin }) => (<div>
    <Paper style={{ marginTop: '20px', height: '270px', width: '90vw', maxWidth: '700px' }}>
      <div style={{ margin: 'auto', width: '300px' }}>
        <p style={{ fontSize: '20px', textAlign: 'left', fontWeight: 'bold', color: '#1A1A1A' }}>Sign In</p>
      </div>
      {failedLogin && <p style={{ color: 'red' }}>The username or password is incorrect</p>}
        <Form>
          <TextField style={{ marginBottom: '25px', display: 'block', margin: 'auto', width: '300px', }} hintText="Username" handleChange={handleChangeUsername} value={username} />
          <TextField password value={password} style={{ display: 'block', margin: 'auto', width: '300px', marginBottom: '20px' }} hintText="Password" handleChange={handleChangePassword} />
          <Button link="#/" label="Login" handleClick={handleSubmitSignIn} />
        </Form>
      <p style={{ marginTop: '40px', color: '#1A1A1A', fontSize: '11px' }}>Don't have an account? <a href="#/signup">Sign up</a></p>
    </Paper>
  </div>)

export default SignIn



