class App extends Component {
  constructor(props) {
    super(props)
    this.state = {
      failedLogin: false,
      signInValidator: [],
      isDogOwner: true,
      credentials: {
        username: 'George',
        password: 'dogsarecool'
      },
      messages: [
        {
          name: 'Jenny',
          picture: george,
          messages: [
            { 
              sent: false, 
              body: 'Hey, I liked your bio!'
            }, 
            { 
              sent: true, 
              body: 'Hey! Thanks! Your dog is really cute!'
            }
          ] 
        }
      ],
      credentialsDatabase: {
        George: { password: 'dogsarecool', isDogOwner: false, 
          messages: [
            {
              name: 'Jenny',
              picture: george,
              messages: [
                { 
                  sent: false, 
                  body: 'Hey, I liked your bio!'
                }, 
                { 
                  sent: true, 
                  body: 'Hey! Thanks! Your dog is really cute!'
                }
              ] 
            }
          ]
        },
        Jenny: { password: 'dogsareneat', isDogOwner: true, 
          messages: [
            {
              name: 'George', 
              picture: george,
              messages: [
                { 
                  sent: true, 
                  body: 'Hey, I liked your bio!'
                }, 
                { 
                  sent: false, 
                  body: 'Hey! Thanks! Your dog is really cute!'
                }
              ] 
            }
          ]
        },
      },
      usernameIn: '',
      passwordIn: '',
      drawerOpen: false,  
    }

    this.handleSubmitSignIn = this.handleSubmitSignIn.bind(this)
    this.handleInputChange = this.handleInputChange.bind(this)
    this.areValid = this.areValid.bind(this)
    this.toggleDrawer = this.toggleDrawer.bind(this)
    this.logout = this.logout.bind(this)
  }

  handleInputChange(e, type) {
    console.log("hello")
    this.setState({ [type]: e.target.value })
  }

  areValid({username, password}, database) {
    const test = username && password && database[username] && database[username].password === password
    if (test) this.setState({ failedLogin: false }) 
      else this.setState({ failedLogin: true })
    return test
  }

  toggleDrawer() {
    window.location.hash === '#/signin' ||
    this.setState({
      drawerOpen: !this.state.drawerOpen
    })
  }

  handleSubmitSignIn() {
    // const hasNumber = (str) => {
    //   const strArr = str.split()
    //   return strArr.reduce((acc, b) => acc || parseInt(b, 10), false)
    // } 
    // alert(hasNumber('1a'))
    const { usernameIn, passwordIn } = this.state;
    const valid = this.areValid({ username: usernameIn, password: passwordIn }, this.state.credentialsDatabase)
    if (valid) {
      this.setState({
        failedLogin: false, 
        credentials: { 
          username: this.state.usernameIn, 
          password: this.state.passwordIn 
        },
        messages: this.state.credentialsDatabase[this.state.usernameIn].messages
      })
      window.location.hash = '#/'
    } else {
      this.setState({ failedLogin: true })
    }
  }

  logout() {
    this.setState({ credentials: { username: '', password: '' } })
    this.toggleDrawer()
  }

  render() {
    return (
      <Router>
        <Drawer sidebar={
          <div style={{ padding: '', backgroundColor: 'white', height: '100vh', width: '300px' }}>
            <div style={{ padding: '10px', fontWeight: 'bold', fontSize: '24px' }}>Menu</div>
            <DrawerItem onClick={this.toggleDrawer} href="#/messages">Messages</DrawerItem>
            <DrawerItem onClick={this.toggleDrawer} href="#/Settings">Settings</DrawerItem>
            <DrawerItem onClick={this.logout} href="#/">Logout</DrawerItem>
          </div>
        } open={this.state.drawerOpen} onSetOpen={this.toggleDrawer} pullRight>
        <div className="App" style={{ minHeight: '100vh', padding: '0' }}>
        <div>
          <Header toggleDrawer={this.toggleDrawer} />
          <Route path="/" exact render={() => (
            (this.state.credentials.username !== '' && this.state.credentials.password !== '') 
              ? <Home 
                isDogOwner={this.state
                  .credentialsDatabase[this.state.credentials.username]
                    .isDogOwner} walkers={sampleWalkers} /> 
              : window.location.hash = '#/signin'
          )} />
          <Route path="/signin" render={() => (
            <SignIn              
              failedLogin={this.state.failedLogin}
              username={this.state.usernameIn}
              handleChangeUsername={(e) => {this.handleInputChange(e, 'usernameIn')}}
              handleChangePassword={(e) => {this.handleInputChange(e, 'passwordIn')}}
              handleSubmitSignIn={this.handleSubmitSignIn} />
          )} />
          <Route path="/messages" render={() => <Messages messages={this.state.messages} />} />
        </div>
        <Footer /></div> </Drawer>
      </Router>
    );
  }
}

export default App;



