import React from 'react'

import paw from '../images/Paw_Print.svg'

import { Paper } from '../Components/Formatting'

import { Button } from '../Components/Inputs'

const Walker = ({ distance, rating, name, picture, description }) => (
  <Paper className="walker" style={{ display: 'block', padding: '0', boxShadow: '' }}>
    <img className="picture" src={picture} style={{ 
      /* float: 'left', 
      margin: 'auto',
      display: 'inline-block', 
      width: '314px',
      border: '3px solid gray', 
      borderRadius: '3px' */ }} />
        <div className="bio" style={{ }}>
          <div className="top" style={{ display: 'inline-block', width: '100%' }}>
            <div style={{ display: 'inline-block', float: 'left', fontWeight: 'bold' }}>
              {name}
            </div>
            <div style={{ display: 'inline-block', float: 'right' }}>
              Rating: <img style={{ height: '10px' }} src={paw} />
              <img style={{ height: '10px' }} src={paw} />
              <img style={{ height: '10px' }} src={paw} />
              <img style={{ height: '10px' }} src={paw} />
              <img style={{ height: '10px' }} src={paw} />
            </div>
          </div>
        <div className="description" style={{ minWidth: '300px', width: '90%' }}>{description}</div>
      </div>
    <div className="geo" style={{
      textAlign: 'center',
      display: 'inline-block',
      fontStyle: 'italic', 
      width: '300px', 
      float: 'bottom',
      marginTop: '50px', 
      marginBottom: '5px'
    }}>
      This user is {distance} from you
    </div>
    <Button className="button" label="Start chatting" style={{ padding: '5px', width: '300px', marginBottom: '10px' }} inline />
  </Paper>)

const WalkerList = ({ walkers }) => (
  <div>
    {walkers.map((walker, index) => (<Walker key={index} {...walker} />))}
  </div>
)

const DogOwner = ({ walkers }) => (<div style={{ marginTop: '10px' }}><WalkerList walkers={walkers} /></div>)

const DogWalker = () => (<div>is dog walker</div>)
 
const Home = ({ isDogOwner, walkers, messages }) => (isDogOwner ? <DogOwner walkers={walkers} /> : <DogWalker />)

export default Home


