import React from 'react'

const Footer = () => (<div style={{
  backgroundColor: '#101933',
  float: 'bottom',
  height: '100px',
  position: 'absolute',
  bottom: window.location.hash === '#/signin' && '0',
  width: '100vw', clear: 'both',
  marginTop: '10px'
}}>
  <p style={{ 
    width: '300px',
    float: 'right',
    fontSize: '11px',
    color: '#FFF',
    textAlign: 'right',
    marginRight: '10px',
  }}>Collr was created as a part of a User Interface class at the University of Cincinnati. It is not a real application, just a demo interface for a project. Find the source code <a style={{ textDecoration: 'none', color: '#00FFFF' }} href="https://github.com/CMinusMinus/dog-walk">here</a>.</p>
</div>)

export default Footer


