import React from 'react'

const Title = ({ children, className, style }) => (
  <div style={{ 
    fontSize: '24px', 
    fontWeight: 'bold', ...style 
  }}>{children}</div>)
  

const Paper = ({ children, minHeight, style, className }) => (
  <div className={className} style={{
    boxShadow: '1px 1px #888, -1px 1px 3px #888', 
    backgroundColor: 'white', 
    minHeight: minHeight,
    display: 'inline-block',
    padding: '5px',
    overflow: 'auto',
    clear: 'both',
    ...style
  }}>
    {children}
  </div>)
  
const Tabs = ({ tabs }) => (
  <div style={{ display: 'inline', float: 'right' }}>
    {tabs.map((tab, index) => (
      <a key={index} style={{
            color: "inherit", 
            height: 'inherit', 
            width: 'inherit', 
            textDecoration: 'inherit' 
          }} href={tab.link}>
      <div
        className="text-center"
        style={{
          width: '102px',
          fontSize: '15px',
          height:'36px',
          color: window.location.hash === tab.link ? 'white' : '',
          backgroundColor: window.location.hash === tab.link ? '#2778CD' : '',
          clear: 'both',
          display: 'inline-block',
          lineHeight: '36px',
          textDecoration: 'none',
        }}>
          
      {tab.name}  
    </div></a>))}
  </div>)
  
const Paragraph = ({ children, style, className }) => (
  <div className={className} style={{ marginBottom: '10px', ...style }}>
    {children}</div>)
  
const InlineText = ({ children, style, left, className }) => (
  <div className={className} style={{ 
    display: 'inline-block', 
    margin: '10px', 
    marginLeft: left ? '0' : '',
    ...style
  }}>{children}</div>)

export { Title, Paragraph, Paper, InlineText, Tabs }


